.edit-profile {
    margin: 0 auto;
    font-family: Roboto;
    color: #2B343B;
    font-weight: 400;
    max-width: 1035px;

    &__wrap {
        display: flex;
    }
}

.edit-profile__form-wrap {
    width: 100%;
}

.edit-profile__avatar {
    margin-right: 48px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.edit-profile__pic {
    width: 180px;
    height: 180px;
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 24px;
}

.edit-profile__img {
    width: 100%;
    height: 100%;
}

